import React, { useEffect, useState } from "react";
import Axios from "axios";
import { Row, Col, Button } from "antd";
import ProductImage from "./Sections/ProductImage";
import ProductInfo from "./Sections/ProductInfo";
import { addToCart } from "../../../_actions/user_actions";
import { useDispatch } from "react-redux";
import { Tabs, notification } from "antd";
import {
  ReadOutlined,
  ScheduleOutlined,
  PushpinOutlined,
} from "@ant-design/icons";
import "./Sections/Details.css";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import Avatar from "antd/lib/avatar/avatar";

const openCartNotification = () => {
  const key = `open${Date.now()}`;
  const btn = (
    <Button type="primary" size="small" onClick={() => notification.close(key)}>
      Continue Booking
    </Button>
  );

  notification.open({
    message: "Item Added to Cart",
    description: "Proceed to Checkout in your Cart",
    type: "success",
    btn,
    key,
  });
};

const { TabPane } = Tabs;

function DetailProductPage(props) {
  const user = useSelector((state) => state.user);

  const dispatch = useDispatch();
  const productId = props.match.params.productId;
  const [Product, setProduct] = useState([]);

  useEffect(() => {
    Axios.get(`/api/product/products_by_id?id=${productId}&type=single`).then(
      (response) => {
        setProduct(response.data[0]);
      }
    );
  }, []);

  const addToCartHandler = (productId) => {
    if (user.userData && !user.userData.isAuth) {
      return alert("Please Log in first");
    }

    dispatch(addToCart(productId));
    openCartNotification();
  };
  console.log(Product);
  //   <ProductInfo addToCart={addToCartHandler} detail={Product} />;

  return (
    <div
      className="postPage"
      style={{ maxWidth: "1200px", margin: "5rem auto", padding: "1rem" }}
    >
      <div style={{ display: "flex", justifyContent: "center" }}>
        <h1>{Product.title}</h1>
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        <div>
          {" "}
          <Avatar src={Product.writer && Product.writer.image} /> a deal from{" "}
          <span style={{ fontSize: "1.2rem", fontWeight: "bold" }}>
            {Product.writer && Product.writer.name}
          </span>{" "}
          <h4> {Product.updatedAt}</h4>
        </div>
        <p style={{ alignSelf: "flex-end" }}>Package Sold: {Product.sold}</p>
      </div>
      <br />

      <Row gutter={[16, 16]}>
        <Col lg={12} xs={24}>
          <ProductImage detail={Product} />
        </Col>
        <Col xs={24} lg={12}>
          <ProductInfo addToCart={addToCartHandler} detail={Product} />
        </Col>
      </Row>

      <Tabs defaultActiveKey="1" centered>
        <TabPane
          tab={
            <span>
              <ReadOutlined />
              Important Details
            </span>
          }
          key="1"
        >
          <pre style={{ whiteSpace: "pre-wrap" }}>{Product.details}</pre>
        </TabPane>
        <TabPane
          tab={
            <span>
              <ScheduleOutlined />
              Detailed Itinerary
            </span>
          }
          key="2"
        >
          <pre style={{ whiteSpace: "pre-wrap" }}>{Product.itinerary}</pre>
        </TabPane>
        <TabPane
          tab={
            <span>
              <PushpinOutlined /> Things to Bring
            </span>
          }
          key="3"
        >
          <pre style={{ whiteSpace: "pre-wrap" }}>{Product.tobring}</pre>
        </TabPane>
      </Tabs>
    </div>
  );
}

export default DetailProductPage;
