import React, { useState } from "react";
import { Button, Modal } from "antd";
import PrivateTripsForm from "../views/PrivateTrips/PrivateTripsForm";

function PrivateTripsModal() {
  const [visible, setVisible] = useState(false);

  return (
    <div>
      <Button
        size="large"
        shape="round"
        type="primary"
        style={{ marginBottom: "2rem" }}
        onClick={() => setVisible(true)}
      >
        Request Private Hike
      </Button>{" "}
      <Modal
        className="PrivateTripsModal"
        title="Come and contact us!"
        centered
        visible={visible}
        onOk={() => setVisible(false)}
        onCancel={() => setVisible(false)}
      >
        <PrivateTripsForm />
      </Modal>
    </div>
  );
}

export default PrivateTripsModal;
