import React, { useState } from "react";
import "./PrivateTrips.css";
import moment from "moment";

import Axios from "axios";
import {
  Form,
  Input,
  Row,
  Col,
  Tooltip,
  Button,
  DatePicker,
  InputNumber,
} from "antd";
import { QuestionCircleOutlined } from "@ant-design/icons";
import { set } from "mongoose";

const { TextArea } = Input;
const { RangePicker } = DatePicker;

function disabledDate(current) {
  // Can not select days before today and today
  return current && current < moment().endOf("day");
}

function PrivateTripsForm() {
  const [NameValue, setNameValue] = useState("");
  const [EmailValue, setEmailValue] = useState("");
  const [PhoneValue, setPhoneValue] = useState("");
  const [DestinationValue, setDestinationValue] = useState("");
  const [MessageValue, setMessageValue] = useState("");
  const [DateValue, setDateValue] = useState(null);
  const [HikersValue, setHikersValue] = useState(null);
  const [form] = Form.useForm();

  const onNameChange = (event) => {
    setNameValue(event.currentTarget.value);
  };
  const onEmailChange = (event) => {
    setEmailValue(event.currentTarget.value);
  };
  const onPhoneChange = (event) => {
    setPhoneValue(event.currentTarget.value);
  };
  const onDestinationChange = (event) => {
    setDestinationValue(event.currentTarget.value);
  };
  const onMessageChange = (event) => {
    setMessageValue(event.currentTarget.value);
  };
  const onHikersChange = (event) => {
    setHikersValue(event.currentTarget.value);
  };

  const onSubmit = () => {
    if (
      !NameValue ||
      !EmailValue ||
      !PhoneValue ||
      !DestinationValue ||
      !DateValue ||
      !HikersValue ||
      !MessageValue
    ) {
      return alert("Fill all Fields");
    }

    const variables = {
      name: NameValue,
      email: EmailValue,
      phone: PhoneValue,
      destination: DestinationValue,
      message: MessageValue,
      date: DateValue,
      hikers: HikersValue,
    };

    Axios.post("/api/message/uploadMessage", variables).then((response) => {
      if (response.data.success) {
        alert("message Sent");
      } else {
        alert("failed to Send message");
      }
    });

    form.resetFields();
  };

  return (
    <Form
      // onSubmit={onSubmit}
      onFinish={onSubmit}
      form={form}
    >
      <Row gutter={16}>
        <Col xs={24} lg={12}>
          <Form.Item
            name="name"
            label={
              <span>
                Name&nbsp;
                <Tooltip title="What do you want us to call you?">
                  <QuestionCircleOutlined />
                </Tooltip>
              </span>
            }
            rules={[
              {
                required: true,
                message: "Please input your name!",
                whitespace: true,
              },
            ]}
          >
            <Input onChange={onNameChange} value={NameValue} />
          </Form.Item>
        </Col>
        <Col xs={24} lg={12}>
          <Form.Item
            name="email"
            label="E-mail"
            rules={[
              {
                type: "email",
                message: "The input is not valid E-mail!",
              },
              {
                required: true,
                message: "Please input your E-mail!",
              },
            ]}
          >
            <Input onChange={onEmailChange} value={EmailValue} />
          </Form.Item>
        </Col>
        <Col xs={24} lg={12}>
          {" "}
          <Form.Item
            name="phone"
            label="Phone Number"
            rules={[
              {
                required: true,
                message: "Please input your phone number!",
              },
              {
                min: 11,
                message: "Must be minimum of 11 numbers.",
              },
              {
                max: 11,
                message: "Must be maximum of 11 numbers.",
              },
            ]}
          >
            <Input onChange={onPhoneChange} value={PhoneValue} />
          </Form.Item>
        </Col>
        <Col xs={24} lg={12}>
          <Form.Item
            name="destination"
            label={
              <span>
                Destination&nbsp;
                <Tooltip title="which mountain you would like to climb?">
                  <QuestionCircleOutlined />
                </Tooltip>
              </span>
            }
            rules={[
              {
                required: true,
                message: "Please input your Destination!",
              },
            ]}
          >
            <Input onChange={onDestinationChange} value={DestinationValue} />
          </Form.Item>
        </Col>
        <Col xs={24} lg={12}>
          <Form.Item
            name="hiker"
            label={
              <span>
                Number of Hikers&nbsp;
                <Tooltip title="how many are you in the group?">
                  <QuestionCircleOutlined />
                </Tooltip>
              </span>
            }
            rules={[
              {
                required: true,
                message: "Please state how many you are?",
                whitespace: true,
              },
            ]}
          >
            <Input
              type="number"
              min={1}
              onChange={onHikersChange}
              value={HikersValue}
            />
          </Form.Item>
        </Col>
        <Col xs={24} lg={12}>
          <Form.Item
            name="date"
            label={<span>Date&nbsp;</span>}
            rules={[
              {
                required: true,
                message: "Please input the Date!",
              },
            ]}
          >
            <RangePicker
              bordered
              allowClear
              onSelect={DateValue}
              onChange={setDateValue}
              disabledDate={disabledDate}
            />
          </Form.Item>
        </Col>

        <Col span={24}>
          {" "}
          <Form.Item
            name="message"
            label="Message"
            rules={[
              {
                required: true,
                message: "Please input your message!",
              },
              {
                min: 20,
                message: "message must be minimum of 20 characters.",
              },
              {
                max: 200,
                message: "message must be maximum of 200 characters.",
              },
            ]}
          >
            <TextArea onChange={onMessageChange} value={MessageValue} />
          </Form.Item>
        </Col>

        <Button
          // onClick={onSubmit}
          type="primary"
          htmlType="submit"
          size="large"
          style={{ margin: "auto", width: "200px" }}
        >
          Submit
        </Button>
      </Row>
    </Form>
  );
}

export default PrivateTripsForm;
