import React, { useEffect, useState } from "react";
import { Button, Col, Row, Modal } from "antd";
import PrivateTripsModal from "../../../utils/PrivateTripsModal";

function ProductInfo(props) {
  const [Product, setProduct] = useState({});

  useEffect(() => {
    setProduct(props.detail);
  }, [props.detail]);

  const addToCarthandler = () => {
    props.addToCart(props.detail._id);
  };
  function formatNumber(num) {
    return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  }

  console.log(props.detail);
  return (
    <div style={{ padding: "1rem", background: "rgba(0,0,0,0.2)" }}>
      <Row>
        <Col xs={24} sm={12}>
          <p>Package Rate: â‚±{formatNumber(`${Product.price}`)}</p>
          <p>Difficulty: {Product.difficulty}/10</p>
          <p>Hike Duration: {Product.duration} day/s</p>
          <p>Location: {Product.location}</p>
          <p>Elevation: {formatNumber(`${Product.elevation}`)} MASL</p>
          <p>
            Total Elevation Gain: {formatNumber(`${Product.totalelevation}`)}{" "}
            meters
          </p>
          <p>
            Total Trek Distance: {formatNumber(`${Product.totaltrekdistance}`)}{" "}
            meters
          </p>
          <img src={Product.img}></img>
        </Col>
        <Col
          xs={24}
          sm={12}
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
            margin: "auto",
            padding: "1rem 0",
          }}
        >
          <PrivateTripsModal />
          <Button
            size="large"
            shape="round"
            type="danger"
            onClick={addToCarthandler}
          >
            Avail Package
          </Button>
        </Col>
      </Row>
      <h2 style={{ display: "flex", justifyContent: "center" }}>Overview</h2>
      <div style={{ textAlign: "justify" }}>{Product.description}</div>
    </div>
  );
}

export default ProductInfo;
